import React, { useContext } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import { FormCheckBox, RequiredPromptField } from "./FormUtils"

const StateTransportMethod = () => {
    const { toggleChecked, someChecked } = useContext(ScenarioContext)

    const DEPS = [
        "intv-transport-method-ambulance",
        "intv-transport-method-helicopter"
    ]

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="states-transport-method"
                    label="Would you transport this patient by air or ground?"
                    checked={someChecked(DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-transport-method-ambulance" label="Ambulance" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-transport-method-helicopter" label="Helicopter" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
        </ul>
    )
}

export default StateTransportMethod