import React, { useContext, useEffect } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import { FormCheckBox, RequiredPromptField } from "./FormUtils"


const ControlShock = () => {
    const {
        buildDepListFromCheckListItemIDs,
        setDerivedCheckListItemValue,
        someChecked,
        shouldDisplayInterventionForm,
        toggleChecked,
        toggleDisplayInterventionForm
    } = useContext(ScenarioContext)

    const CONTROL_SHOCK_DEPS = [
        "intv-control-shock-keep-patient-warm",
        "intv-control-shock-administer-high-concentration-o2",
        "intv-control-shock-place-patient-supine-position",
        "intv-control-shock-rapid-transport",
        "intv-control-shock-administer-iv-boluses"
    ]

    const NUM_IVS_DEPS = [
        "intv-control-shock-num-ivs-one",
        "intv-control-shock-num-ivs-two",
        "intv-control-shock-num-ivs-other"
    ]

    const IV_SIZE_DEPS = [
        "intv-control-shock-iv-size-large-as-possible",
        "intv-control-shock-iv-size-other"
    ]

    const IV_FLUID_DEPS = [
        "intv-control-shock-iv-fluid-saline",
        "intv-control-shock-iv-fluid-other"
    ]

    const IV_VOLUME_DEPS = [
        "intv-control-shock-iv-volume-20-cc-per-kilo",
        "intv-control-shock-iv-volume-other"
    ]

    const IV_TARGET_BP_DEPS = [
        "intv-control-shock-iv-target-bp-80-90",
        "intv-control-shock-iv-target-bp-other"
    ]

    const IV_DEPS = [
        "intv-control-shock-num-ivs",
        "intv-control-shock-iv-size",
        "intv-control-shock-iv-fluid",
        "intv-control-shock-iv-volume",
        "intv-control-shock-iv-target-bp"
    ]

    useEffect(
        () => {
            setDerivedCheckListItemValue("intv-control-shock-administer-iv-boluses", "all", IV_DEPS)
        },
        [  buildDepListFromCheckListItemIDs(IV_DEPS)]
    )

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="control-shock"
                    label="What techniques will you use to manage shock?"
                    checked={someChecked(CONTROL_SHOCK_DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-control-shock-keep-patient-warm"                label="Keep patient warm (e.g., apply blankets)" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-control-shock-administer-high-concentration-o2" label="Administer high concentration of oxygen" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-control-shock-place-patient-supine-position"    label="Place or maintain patient in supine position" clickHandler={toggleChecked} /></li>
                    <li>
                        <FormCheckBox id="intv-control-shock-administer-iv-boluses" label="Administer IV fluid resuscitation" clickHandler={toggleDisplayInterventionForm} />
                        {shouldDisplayInterventionForm("intv-control-shock-administer-iv-boluses") && (
                        <ul>
                            <li>
                                <RequiredPromptField
                                    id="intv-control-shock-num-ivs"
                                    label="How many IVs would you establish?"
                                    checked={someChecked(NUM_IVS_DEPS)}
                                />
                                <ul>
                                    <li><FormCheckBox id="intv-control-shock-num-ivs-one" label="one" clickHandler={toggleChecked}/></li>
                                    <li><FormCheckBox id="intv-control-shock-num-ivs-two" label="two" clickHandler={toggleChecked} /></li>
                                    <li><FormCheckBox id="intv-control-shock-num-ivs-other" label="Other" clickHandler={toggleChecked} /></li>
                                </ul>
                            </li>
                            <li>
                                <RequiredPromptField
                                    id="intv-control-shock-iv-fluid"
                                    label="What type of fluid?"
                                    checked={someChecked(IV_FLUID_DEPS)}
                                />
                                <ul>
                                    <li><FormCheckBox id="intv-control-shock-iv-fluid-saline" label="Normal Saline" clickHandler={toggleChecked} /></li>
                                    <li><FormCheckBox id="intv-control-shock-iv-fluid-other" label="Other" clickHandler={toggleChecked} /></li>
                                </ul>
                            </li>
                            <li>
                                <RequiredPromptField
                                    id="intv-control-shock-iv-size"
                                    label="What size IV?"
                                    checked={someChecked(IV_SIZE_DEPS)}
                                />
                                <ul>
                                    <li><FormCheckBox id="intv-control-shock-iv-size-large-as-possible" label="As large as possible" clickHandler={toggleChecked} /></li>
                                    <li><FormCheckBox id="intv-control-shock-iv-size-other" label="Other" clickHandler={toggleChecked} /></li>
                                </ul>
                            </li>
                            <li>
                                <RequiredPromptField
                                    id="intv-control-shock-iv-volume"
                                    label="At what volume?"
                                    checked={someChecked(IV_VOLUME_DEPS)}
                                />
                                <ul>
                                    <li><FormCheckBox id="intv-control-shock-iv-volume-20-cc-per-kilo" label="20 cc/kilo" clickHandler={toggleChecked} /></li>
                                    <li><FormCheckBox id="intv-control-shock-iv-volume-other" label="Other" clickHandler={toggleChecked} /></li>
                                </ul>
                            </li>
                            <li>
                                <RequiredPromptField
                                    id="intv-control-shock-iv-target-bp"
                                    label="What is your target BP?"
                                    checked={someChecked(IV_TARGET_BP_DEPS)}
                                />
                                <ul>
                                    <li><FormCheckBox id="intv-control-shock-iv-target-bp-80-90" label="SBP 80-90, if bleeding is controlled" clickHandler={toggleChecked} /></li>
                                    <li><FormCheckBox id="intv-control-shock-iv-target-bp-other" label="Other" clickHandler={toggleChecked} /></li>
                                </ul>
                            </li>
                        </ul>
                        )}
                    </li>
                </ul>
            </li>
        </ul>
    )
}


export default ControlShock