
import React, { useContext, useEffect } from 'react';
import { ScenarioContext } from "../../contexts/ScenarioContext";

import { FormCheckBox, RequiredPromptField} from "./FormUtils"


const AssuresAdequateVentilation = () => {
    const {
        buildDepListFromCheckListItemIDs,
        setDerivedCheckListItemValue,
        someChecked,
        toggleChecked,
    } = useContext(ScenarioContext);

    const VENT_TECH_DEPS = [
        "intv-ventilation-technique-bag-valve-mask",
        "intv-ventilation-technique-automatic-transport-ventilator"
    ]

    const VENT_RATE_DEPS = [
        "intv-ventilation-rate-10-12-bpm",
        "intv-ventilation-rate-other"
    ]

    const VENT_DEPS = [
        "intv-ventilation-technique",
        "intv-ventilation-rate"
    ]

    useEffect(
        () => {
            setDerivedCheckListItemValue("assures-adequate-ventilation", "all", VENT_DEPS)
        },
        [ buildDepListFromCheckListItemIDs(VENT_DEPS)]
    )

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="intv-ventilation-technique"
                    label="What ventilation technique will you use?"
                    checked={someChecked(VENT_TECH_DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-ventilation-technique-bag-valve-mask" label="Bag-valve mask" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-ventilation-technique-automatic-transport-ventilator" label="Automatic transport ventilator" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
            <li>
                <RequiredPromptField
                    id="intv-ventilation-rate"
                    label="At what rate will you ventilate the patient?"
                    checked={someChecked(VENT_RATE_DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-ventilation-rate-10-12-bpm" label="10-12 breaths/minute" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-ventilation-rate-other" label="Other" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
        </ul>
    );
}

export default AssuresAdequateVentilation;