import React, { useContext } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import { FormCheckBox, RequiredPromptField } from "./FormUtils"

const TransfersPatient = () => {
    const { toggleChecked, someChecked } = useContext(ScenarioContext)

    const DEPS = [
        "intv-transfer-to-ambulance-walk-patient",
        "intv-transfer-to-ambulance-scoop-stretcher",
        "intv-transfer-to-ambulance-transfer-to-stretcher-on-long-backboard",
        "intv-transfer-to-ambulance-other"
    ]

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="transfers-patient"
                    label="What method will you use to transfer the patient to the ambulance?"
                    checked={someChecked(DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-transfer-to-ambulance-walk-patient" label="Walk patient to ambulance" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-transfer-to-ambulance-scoop-stretcher" label="Use scoop stretcher" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-transfer-to-ambulance-transfer-to-stretcher-on-long-backboard" label="Transfer to stretcher on long backboard" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-transfer-to-ambulance-other" label="Other" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
        </ul>
    )
}


export default TransfersPatient