import React, { useContext } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import { FormCheckBox, RequiredPromptField } from "./FormUtils"

const StatePatientStatus = () => {
    const { toggleChecked, someChecked } = useContext(ScenarioContext)

    const DEPS = [
        "intv-is-load-and-go-yes",
        "intv-is-load-and-go-no"
    ]

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="states-patient-status"
                    label="Is this a load and go patient?"
                    checked={someChecked(DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-is-load-and-go-yes" label="Yes" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-is-load-and-go-no" label="No" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
        </ul>
    )
}

export default StatePatientStatus