import React, { useContext, useEffect } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import { FormCheckBox, RequiredPromptField } from "./FormUtils"


const ManageBreathingInjury = () => {
    const {
        buildDepListFromCheckListItemIDs,
        setDerivedCheckListItemValue,
        shouldDisplayInterventionForm,
        someChecked,
        toggleChecked,
        toggleDisplayInterventionForm
    } = useContext(ScenarioContext)

    const MANAGE_BREATHING_DEPS = [
        "intv-manage-breathing-apply-occlusive-dressing",
        "intv-perform-pleural-decompression"
    ]

    const OCCLUSIVE_DRESSING_METHOD_DEPS = [
        "intv-manage-breating-apply-occulative-dressing-method-leave-one-side-untaped",
        "intv-manage-breating-apply-occulative-dressing-method-other"
    ]

    const NEEDLE_PLACEMENT_DEPS = [
        "intv-pleural-decompression-needle-placement-2nd-intercostal-space-mid-clavicular",
        "intv-pleural-decompression-needle-placement-4th-5th-intercostal-space",
        "intv-pleural-decompression-needle-placement-other"
    ]

    const NEEDLE_SIZE_DEPS = [
        "intv-pleural-decompression-needle-size-correct",
        "intv-pleural-decompression-needle-size-other"
    ]

    const PLEURAL_DECOMPRES_DEPS = [
        "intv-perform-pleural-decompression-needle-placement",
        "intv-perform-pleural-decompression-needle-size",
    ]


    useEffect(
        () => { setDerivedCheckListItemValue("intv-manage-breathing-apply-occlusive-dressing", "some", OCCLUSIVE_DRESSING_METHOD_DEPS)},
        [ buildDepListFromCheckListItemIDs(OCCLUSIVE_DRESSING_METHOD_DEPS) ]
    )

    useEffect(
        () => { setDerivedCheckListItemValue("intv-perform-pleural-decompression", "all", PLEURAL_DECOMPRES_DEPS) },
        [ buildDepListFromCheckListItemIDs(PLEURAL_DECOMPRES_DEPS) ]
    )


    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="manage-breathing-injury"
                    label="What techniques would you use to treat identified life-threatening injuries?"
                    checked={someChecked(MANAGE_BREATHING_DEPS)}
                />
                <ul>
                    <li>
                        <FormCheckBox id="intv-manage-breathing-apply-occlusive-dressing" label="Apply occlusive dressing to breathing-compromising wounds" clickHandler={toggleDisplayInterventionForm} />
                        {shouldDisplayInterventionForm("intv-manage-breathing-apply-occlusive-dressing") && (
                            <ul>
                                <li><i>How would you create a release valve on an occlusive dressing?</i>
                                    <ul>
                                        <li><FormCheckBox id="intv-manage-breating-apply-occulative-dressing-method-leave-one-side-untaped" label="Leave one side untaped" clickHandler={toggleChecked} /></li>
                                        <li><FormCheckBox id="intv-manage-breating-apply-occulative-dressing-method-other" label="Other" clickHandler={toggleChecked} /></li>
                                    </ul>
                                </li>

                            </ul>
                        )}
                    </li>
                    <li>
                        <FormCheckBox id="intv-perform-pleural-decompression" label="Perform pleural decompression" clickHandler={toggleDisplayInterventionForm} />
                        {shouldDisplayInterventionForm("intv-perform-pleural-decompression") && (
                        <ul>
                            <li>
                                <RequiredPromptField
                                    id="intv-perform-pleural-decompression-needle-placement"
                                    label="Where would you place the needle?"
                                    checked={someChecked(NEEDLE_PLACEMENT_DEPS)}
                                />
                                <ul>
                                    <li><FormCheckBox id="intv-pleural-decompression-needle-placement-2nd-intercostal-space-mid-clavicular" label="Second intercostal space mid-clavicular" clickHandler={toggleChecked} /></li>
                                    <li><FormCheckBox id="intv-pleural-decompression-needle-placement-4th-5th-intercostal-space"            label="Fourth/fifth intercostal space" clickHandler={toggleChecked} /></li>
                                    <li><FormCheckBox id="intv-pleural-decompression-needle-placement-other"                                label="Other" clickHandler={toggleChecked} /></li>
                                </ul>
                            </li>
                            <li>
                                <RequiredPromptField
                                    id="intv-perform-pleural-decompression-needle-size"
                                    label="What size needle do you want to use?"
                                    checked={someChecked(NEEDLE_SIZE_DEPS)}
                                />
                                <ul>
                                    <li><FormCheckBox id="intv-pleural-decompression-needle-size-correct" label="Correct answer" clickHandler={toggleChecked} /></li>
                                    <li><FormCheckBox id="intv-pleural-decompression-needle-size-other" label="Other" clickHandler={toggleChecked} /></li>
                                </ul>
                            </li>
                        </ul>
                        )}
                    </li>
                </ul>
            </li>
        </ul>
    )
}

export default ManageBreathingInjury