import React, { useContext } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import { FormCheckBox, RequiredPromptField } from "./FormUtils"

const MOD_INTV_DEPS = [
    "intv-modify-intervention-replace-basic-airway-with-advanced-or-intubate",
    "intv-modify-intervention-replace-bandages",
    "intv-modify-intervention-replace-oxygen-tank",
    "intv-modify-intervention-adjust-iv-replace-bolus",
    "intv-modify-intervention-turn-on-heat-in-ambulance",
    "intv-modify-intervention-other-options"
]

const ReassessModifyInterventions = () => {
    const { toggleChecked, someChecked } = useContext(ScenarioContext)

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="reassess-modify-interventions"
                    label="What interventions will you reassess or modify, and in what order?"
                    checked={someChecked(MOD_INTV_DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-modify-intervention-replace-basic-airway-with-advanced-or-intubate" label="Replace basic airway with advanced airway, or intubate" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-modify-intervention-replace-bandages" label="Replace bandages" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-modify-intervention-replace-oxygen-tank" label="Replace oxygen tank" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-modify-intervention-adjust-iv-replace-bolus" label="Adjust IV, hang second bag" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-modify-intervention-turn-on-heat-in-ambulance" label="Turn on heat in ambulance" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-modify-intervention-other-options" label="other-options" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
        </ul>
    )
}

export default ReassessModifyInterventions