import React, { useContext } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import { FormCheckBox, RequiredPromptField } from "./FormUtils"

const StateLightSirenDecision = () => {
    const { toggleChecked, someChecked} = useContext(ScenarioContext)

    const DEPS = [
        "intv-transport-lights-sirens-yes",
        "intv-transport-lights-sirens-no"
    ]

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="states-lights-siren-decision"
                    label="Would you use lights and sirens with this patient?"
                    checked={someChecked(DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-transport-lights-sirens-yes" label="Yes" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-transport-lights-sirens-no" label="No" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
        </ul>
    )
}

export default StateLightSirenDecision