import React, { useContext } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import { FormCheckBox, RequiredPromptField } from "./FormUtils"

const RestrictSpinalMotion = () => {
    const { toggleChecked, someChecked } = useContext(ScenarioContext)

    const DEPS = [
        "intv-spinal-immobilization-manual-c-spine",
        "intv-spinal-immobilization-ked",
        "intv-spinal-immobilization-lbb",
        "intv-spinal-immobilization-cervical-collar",
        "intv-spinal-immobilization-blocks"
    ]

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="restricts-spinal-motion"
                    label="What spinal immobilization techniques will you apply, and in which order?"
                    checked={someChecked(DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-spinal-immobilization-manual-c-spine" label="Manual c-spine stabilization" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-spinal-immobilization-ked" label="Seated spinal motion restriction (KED)" clickHandler={toggleChecked}  /></li>
                    <li><FormCheckBox id="intv-spinal-immobilization-lbb" label="Supine long back board (LBB)" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-spinal-immobilization-cervical-collar" label="Cervical collar" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-spinal-immobilization-blocks" label="Cervical immobilization device (blocks)" clickHandler={toggleChecked}  /></li>
                </ul>
            </li>
        </ul>
    )
}


export default RestrictSpinalMotion