import React, { useContext } from "react"

import AdministerAdditionalInterventions from "./forms/AdministerAdditionalInterventions"
import AssuresAdequateVentilation from "./forms/AssuresAdequateVentilation"
import ControlBleeding from "./forms/ControlBleeding"
import ControlShock from "./forms/ControlShock"
import InsertsAdjunct from "./forms/InsertsAdjunct"
import ManageBreathingInjury from "./forms/ManageBreathingInjury"
import ManageSecondaryInjuries from "./forms/ManageSecondaryInjuries"
import ObtainsAirwayPatency from "./forms/ObtainsAirwayPatency"
import OxygenTherapy from "./forms/OxygenTherapy"
import ReassessModifyInterventions from "./forms/ReassessModifyInterventions"
import ReassessVitalSigns from "./forms/ReassessVitalSigns"
import RestrictSpinalMotion from "./forms/RestrictSpinalMotion"
import StateFacilityChoice from "./forms/StateFacilityChoice"
import StateLightsSirenDecision from "./forms/StateLightsSirenDecision"
import StatePatientStatus from "./forms/StatePatientStatus"
import StateTransportMethod from "./forms/StateTransportMethod"
import TransfersPatient  from "./forms/TransfersPatient"


const InterventionForm = ({form}) => {

    switch(form) {
        case "administer-additional-interventions":
            return (<AdministerAdditionalInterventions />)
        case "assures-adequate-ventilation":
            return (<AssuresAdequateVentilation />)
        case "control-bleeding":
            return (<ControlBleeding />)
        case "control-shock":
            return (<ControlShock />)
        case "inserts-adjunct":
            return (<InsertsAdjunct />)
        case "manage-breathing-injury":
            return (<ManageBreathingInjury />)
        case "manage-secondary-injuries":
            return (<ManageSecondaryInjuries />)
        case "obtains-airway-patency":
            return (<ObtainsAirwayPatency />)
        case "oxygen-therapy":
            return (<OxygenTherapy />)
        case "reassess-modify-interventions":
            return (<ReassessModifyInterventions />)
        case "reassess-vital-signs":
            return (<ReassessVitalSigns />)
        case "restricts-spinal-motion":
            return (<RestrictSpinalMotion />)
        case "states-facility-choice":
            return (<StateFacilityChoice />)
        case "states-lights-siren-decision":
            return (<StateLightsSirenDecision />)
        case "states-patient-status":
            return (<StatePatientStatus />)
        case "states-transport-method":
            return (<StateTransportMethod />)
        case "transfers-patient":
            return (<TransfersPatient />)
        default:
            return (<div>unimplemented form</div>)
    }
}


