import React, {useContext, useEffect} from "react"

import {ScenarioContext} from "../../contexts/ScenarioContext"

import {FormCheckBox, RequiredPromptField} from "./FormUtils"

const MedicationFacilititatedIntubation = () => {
    const {
        buildDepListFromCheckListItemIDs,
        toggleChecked,
        setDerivedCheckListItemValue,
        someChecked,
    } = useContext(ScenarioContext)

    const MED_CHOICE_DEPS = [
        "intv-sedated-intubation-medication-etomidate",
        "intv-sedated-intubation-medication-ketamine",
        "intv-sedated-intubation-medication-propofol",
        "intv-sedated-intubation-medication-other"
    ]

    const DOSAGE_DEPS = [
        "intv-sedated-intubation-dosage-option-1",
        "intv-sedated-intubation-dosage-other"
    ]

    const MED_ASSISTED_DEPS = [
        "intv-sedated-intubation-medication",
        "intv-sedated-intubation-dosage"
    ]

    useEffect(
        () => { setDerivedCheckListItemValue("intv-intubation-type-medication-facilitated", "all", MED_ASSISTED_DEPS) },
        [ buildDepListFromCheckListItemIDs(MED_ASSISTED_DEPS) ]
    )

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="intv-sedated-intubation-medication"
                    label="What medications would you administer?"
                    checked={someChecked(MED_CHOICE_DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-sedated-intubation-medication-etomidate" label="Etomidate" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-sedated-intubation-medication-ketamine" label="Ketamine" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-sedated-intubation-medication-midazolam" label="Midazolam" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-sedated-intubation-medication-other" label="Other" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
            <li>
                <RequiredPromptField
                    id="intv-sedated-intubation-dosage"
                    label="What dose?"
                    checked={someChecked(DOSAGE_DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-sedated-intubation-dosage-option-1" label="scenario-specific-option" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-sedated-intubation-dosage-other" label="Other" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
        </ul>
    )
}

const OrotrachealIntubation = () => {
    const {
        buildDepListFromCheckListItemIDs,
        setDerivedCheckListItemValue,
        shouldDisplayInterventionForm,
        someChecked,
        toggleChecked,
        toggleDisplayInterventionForm,
    } = useContext(ScenarioContext)

    const INTU_TYPE_DEPS = [
        "intv-intubation-type-direct",
        "intv-intubation-type-gum-elastic-bougie-assisted",
        "intv-intubation-type-video-laryngoscopy",
        "intv-intubation-type-medication-facilitated"
    ]

    const TUBE_SIZE_DEPS = [
        "intv-intubation-tube-size-option-1",
        "intv-intubation-tube-size-other"
    ]

    const CONFIRM_TUBE_PLACEMENT_DEPS = [
        "intv-inspect-tube-location",
        "intv-co2-capnography",
        "intv-auscultate-epigastrium",
        "intv-check-oxygen-saturation"
    ]

    const OROTRACHEAL_DEPS = [
        "intv-intubation-type",
        "intv-intubation-tube-size",
        "intv-intubation-confirm-correct-tube-placement"
    ]
    useEffect(
        () => { setDerivedCheckListItemValue("intv-orotracheal-intubation", "all", OROTRACHEAL_DEPS) },
        [ buildDepListFromCheckListItemIDs(OROTRACHEAL_DEPS) ]
    )

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="intv-intubation-type"
                    label="Which type of intubation would you perform?"
                    checked={someChecked(INTU_TYPE_DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-intubation-type-direct" label="Direct" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-intubation-type-gum-elastic-bougie-assisted" label="Gum elastic bougie-assisted" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-intubation-type-video-laryngoscopy" label="Video laryngoscopy" clickHandler={toggleChecked} /></li>
                    <li>
                        <FormCheckBox id="intv-intubation-type-medication-facilitated" label="Medication facilitated" clickHandler={toggleDisplayInterventionForm} />
                        {shouldDisplayInterventionForm("intv-intubation-type-medication-facilitated") && (
                            <MedicationFacilititatedIntubation />
                        )}
                    </li>
                </ul>
            </li>
            <li>
                <RequiredPromptField
                    id="intv-intubation-tube-size"
                    label="What size tube would you use?"
                    checked={someChecked(TUBE_SIZE_DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-intubation-tube-size-option-1" label="scenario-specific-option" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-intubation-tube-size-other" label="Other" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
            <li>
                <RequiredPromptField
                    id="intv-intubation-confirm-correct-tube-placement"
                    label="What techniques will you use to confirm correct tube placement?"
                    checked={someChecked(CONFIRM_TUBE_PLACEMENT_DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-inspect-tube-location" label="Inspect tube location" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-co2-capnography" label="CO2 capnography" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-auscultate-epigastrium" label="Auscultate bilaterally and over epigastrium" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-check-oxygen-saturation" label="Check oxygen saturation" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
        </ul>
    )
}


const InsertsAdjunct = () => {

    const {
        buildDepListFromCheckListItemIDs,
        isChecked,
        setDerivedCheckListItemValue,
        someChecked,
        shouldDisplayInterventionForm,
        toggleChecked,
        toggleDisplayInterventionForm,
    } = useContext(ScenarioContext)

    const OPEN_AIRWAY_DEPS = [
        "intv-open-airway-method-head-tilt",
        "intv-open-airway-method-jaw-thrust",
        "intv-open-airway-method-modified-jaw-thrust",
        "intv-open-airway-method-other"
    ]

    const BASIC_TYPE_DEPS = [
        "intv-nasopharyngeal",
        "intv-oropharyngeal",
    ]

    const NASO_REASONING_DEPS = [
        "intv-basic-airway-nasopharyngeal-reason-gag-reflex",
        "intv-basic-airway-nasopharyngeal-reason-other"
    ]

    const BASIC_DEPS = [
        "intv-basic-airway-type",
        "intv-basic-airway-nasopharyngeal-reason"
    ]

    const ADVANCED_DEPS = [
        "intv-supraglottic",
        "intv-orotracheal-intubation"
    ]

    const ADJUNCT_TYPE_DEPS = [
        "intv-basic-airway",
        "intv-advanced-airway"
    ]


    const INSERTS_ADJUNCT_DEPS = [
        "intv-open-airway-method",
        "intv-adjunct-type"
    ]

    useEffect(
        () => { setDerivedCheckListItemValue("intv-basic-airway-type", "some", BASIC_TYPE_DEPS) },
        [ buildDepListFromCheckListItemIDs(BASIC_TYPE_DEPS) ]
    )

    useEffect(
        () => { setDerivedCheckListItemValue("intv-basic-airway", "all", BASIC_DEPS) },
        [ buildDepListFromCheckListItemIDs(BASIC_DEPS) ]
    )

    useEffect(
        () => { setDerivedCheckListItemValue("intv-advanced-airway", "some", ADVANCED_DEPS) },
        [ buildDepListFromCheckListItemIDs(ADVANCED_DEPS) ]
    )

    useEffect(
        () => { setDerivedCheckListItemValue("intv-adjunct-type", "some", ADJUNCT_TYPE_DEPS) },
        [ buildDepListFromCheckListItemIDs(ADJUNCT_TYPE_DEPS) ]
    )

    useEffect(
        () => { setDerivedCheckListItemValue("inserts-adjunct", "all", INSERTS_ADJUNCT_DEPS) },
        [ buildDepListFromCheckListItemIDs(INSERTS_ADJUNCT_DEPS) ]
    )


    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="intv-open-airway-method"
                    label="What method would you use to open the airway?"
                    checked={someChecked(OPEN_AIRWAY_DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-open-airway-method-head-tilt" label="Head tilt" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-open-airway-method-jaw-thrust" label="Jaw thrust" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-open-airway-method-modified-jaw-thrust" label="Modified jaw thrust" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-open-airway-method-other" label="Other" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
            <li><i>What type of adjunct would you try to apply, and in what order?</i></li>
            <li>
                <b>Basic</b>
                <ul>
                    <li><FormCheckBox id="intv-nasopharyngeal" label="Nasopharyngeal" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-oropharyngeal" label="Oropharyngeal" clickHandler={toggleChecked} /></li>
                    {isChecked("intv-basic-airway-type") && (
                    <li>
                        <RequiredPromptField
                            id="intv-basic-airway-nasopharyngeal-reason"
                            label="When would you use a nasopharyngeal airway instead of an oropharyngeal airway?"
                            checked={someChecked(NASO_REASONING_DEPS)}
                        />
                        <ul>
                            <li><FormCheckBox id="intv-basic-airway-nasopharyngeal-reason-gag-reflex" label="When patient has a gag reflex" clickHandler={toggleChecked} /></li>
                            <li><FormCheckBox id="intv-basic-airway-nasopharyngeal-reason-other" label="Other" clickHandler={toggleChecked} /></li>

                        </ul>
                    </li>
                    )}
                </ul>
            </li>
            <li>
                <b>Advanced</b>
                <ul>
                    <li><FormCheckBox id="intv-supraglottic" label="Supraglottic" clickHandler={toggleChecked} /></li>
                    <li>
                        <FormCheckBox id="intv-orotracheal-intubation" label="Orotracheal Intubation" clickHandler={toggleDisplayInterventionForm} />
                        {shouldDisplayInterventionForm("intv-orotracheal-intubation") && (
                            <OrotrachealIntubation />
                        )}
                    </li>
                </ul>
            </li>
        </ul>
    )
}


export default InsertsAdjunct