import React, { useContext, useEffect} from "react"

import {ScenarioContext} from "../../contexts/ScenarioContext"


export const RequiredPromptField = ({ id, label, checked }) => {
    const { setChecked } = useContext(ScenarioContext)
    useEffect(
        () => {
            setChecked(id, checked)
        },
        [checked]
    )
    return (
        <i>{label}</i>
    )
}


export const FormCheckBox = ({id, label, clickHandler}) => {
    const { isChecked } = useContext(ScenarioContext)

    const whichIcon = isChecked(id) ? "check_box" : "check_box_outline_blank"

    return (
        <span onClick={() => clickHandler(id)}>
            <span
                className="checkbox-icon material-icons-outlined"
            >
                {whichIcon}
            </span>
            <span>
                {label}
            </span>
        </span>
    )
}
