import React, {useContext} from "react"
import { ScenarioContext } from "../../contexts/ScenarioContext"

import { FormCheckBox, RequiredPromptField } from "./FormUtils"

const ManageSecondaryInjuries = () => {
    const { toggleChecked, someChecked } = useContext(ScenarioContext)

    const DEPS = [
        "intv-manage-secondary-injuries-apply-dressings",
        "intv-manage-secondary-injuries-split-fractures",
        "intv-manage-secondary-injuries-other"    ]

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="manage-secondary-injuries"
                    label="What technique(s) will you use to manage secondary wounds/injuries?"
                    checked={someChecked(DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-manage-secondary-injuries-apply-dressings" label="Apply dressings" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-manage-secondary-injuries-split-fractures" label="Splint fractures" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-manage-secondary-injuries-other" label="Other" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
        </ul>
    )
}

export default ManageSecondaryInjuries