import React, { useContext } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import { FormCheckBox, RequiredPromptField } from "./FormUtils"

const StateFacilityChoice = () => {
    const { toggleChecked, someChecked } = useContext(ScenarioContext)

    const DEPS = [
        "intv-facility-choice-trauma-center",
        "intv-facility-choice-closest",
        "intv-facility-choice-other"
    ]

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="states-facility-choice"
                    label="What care facility will you transport the patient to?"
                    checked={someChecked(DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-facility-choice-trauma-center" label="Trauma center" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-facility-choice-closest" label="Closest receiving facility" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-facility-choice-other" label="Other" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
        </ul>
    )
}

export default StateFacilityChoice