import React, { useContext, useEffect } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import { FormCheckBox, RequiredPromptField } from "./FormUtils"


const ObtainsAirwayPatency = () => {
    const {
        buildDepListFromCheckListItemIDs,
        setDerivedCheckListItemValue,
        shouldDisplayInterventionForm,
        someChecked,
        toggleDisplayInterventionForm,
        toggleChecked
    } = useContext(ScenarioContext)


    const SUCTION_CATHETER_TYPE_DEPS = [
        "intv-airway-patency-suction-tip-catheter",
        "intv-airway-patency-suction-little-sucker",
        "intv-airway-patency-suction-yankauer",
        "intv-airway-patency-suction-long-multiuse"
    ]

    const AIRWAY_PATENCY_DEPS = [
        "intv-airway-patency-suction",
        "intv-airway-patency-remove-foreign-body-obstructions",
        "intv-airway-patency-perform-cricothyrotomy"
    ]

    const REMOVE_OBJECT_DEPS = [
        "intv-airway-patency-remove-foreign-body-obstructions-manually",
        "intv-airway-patency-remove-foreign-body-obstructions-magill-forceps"
    ]

    const PERFORM_CRICO_DEPS = [
        "intv-airway-patency-perform-cricothyrotomy-percutaneous",
        "intv-airway-patency-perform-cricothyrotomy-surgical"
    ]

    useEffect(
        () => { setDerivedCheckListItemValue("intv-airway-patency-suction", "some", SUCTION_CATHETER_TYPE_DEPS) },
        [ buildDepListFromCheckListItemIDs(SUCTION_CATHETER_TYPE_DEPS) ]
    )


    useEffect(
        () => { setDerivedCheckListItemValue("intv-airway-patency-remove-foreign-body-obstructions", "some", REMOVE_OBJECT_DEPS) },
        [ buildDepListFromCheckListItemIDs(REMOVE_OBJECT_DEPS) ]
    )


    useEffect(
        () => { setDerivedCheckListItemValue("intv-airway-patency-perform-cricothyrotomy", "some", PERFORM_CRICO_DEPS) },
        [ buildDepListFromCheckListItemIDs(PERFORM_CRICO_DEPS) ]
    )


    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="obtains-airway-patency"
                    label="What techniques will you try, in what order?"
                    checked={someChecked(AIRWAY_PATENCY_DEPS)}
                />
                <ul>
                    <li>
                        <FormCheckBox id="intv-airway-patency-suction" label="Suction Airway" clickHandler={toggleDisplayInterventionForm} />
                        {shouldDisplayInterventionForm("intv-airway-patency-suction") && (
                        <ul>
                            <li><i>What kind of suction catheter will you use?</i>
                                <ul>
                                    <li><FormCheckBox id="intv-airway-patency-suction-tip-catheter" label="Tip Catheter" clickHandler={toggleChecked} /></li>
                                    <li><FormCheckBox id="intv-airway-patency-suction-little-sucker" label="Little Sucker" clickHandler={toggleChecked} /></li>
                                    <li><FormCheckBox id="intv-airway-patency-suction-yankauer" label="Yankauer" clickHandler={toggleChecked} /></li>
                                    <li><FormCheckBox id="intv-airway-patency-suction-long-multiuse" label="Long, multiuse" clickHandler={toggleChecked} /></li>
                                </ul>
                            </li>
                        </ul>
                        )}
                    </li>
                    <li>
                        <FormCheckBox id="intv-airway-patency-remove-foreign-body-obstructions" label="Remove foreign body obstructions" clickHandler={toggleDisplayInterventionForm} />
                        {shouldDisplayInterventionForm("intv-airway-patency-remove-foreign-body-obstructions") && (
                            <ul>
                                <li><i>What technique(s) will you try, and in what order?</i>
                                    <ul>
                                        <li><FormCheckBox id="intv-airway-patency-remove-foreign-body-obstructions-manually" label="Manual removal" clickHandler={toggleChecked} /></li>
                                        <li><FormCheckBox id="intv-airway-patency-remove-foreign-body-obstructions-magill-forceps" label="Magill forceps assisted removal" clickHandler={toggleChecked} /></li>
                                    </ul>
                                </li>
                            </ul>
                        )}
                    </li>
                    <li>
                        <FormCheckBox id="intv-airway-patency-perform-cricothyrotomy" label="Perform cricothyrotomy" clickHandler={toggleDisplayInterventionForm} />
                        {shouldDisplayInterventionForm("intv-airway-patency-perform-cricothyrotomy") && (
                            <ul>
                                <li><i>Which type?</i>
                                    <ul>
                                        <li><FormCheckBox id="intv-airway-patency-perform-cricothyrotomy-percutaneous" label="Percutaneous" clickHandler={toggleChecked} /></li>
                                        <li><FormCheckBox id="intv-airway-patency-perform-cricothyrotomy-surgical" label="Surgical" clickHandler={toggleChecked} /></li>
                                    </ul>
                                </li>
                            </ul>
                        )}
                    </li>
                </ul>
            </li>
        </ul>
    )
}


export default ObtainsAirwayPatency

