import React, { useContext } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import { FormCheckBox, RequiredPromptField } from "./FormUtils"

const ReassessVitalSigns = () => {
    const { toggleChecked, someChecked } = useContext(ScenarioContext)

    const DEPS = [
        "intv-reassess-vitals-frequency-5-min",
        "intv-reassess-vitals-frequency-15-min",
        "intv-reassess-vitals-frequency-other",
    ]

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="reassess-vital-signs"
                    label="How often will you reassess vitals?"
                    checked={someChecked(DEPS)}
                />
                <ul>
                    <li><FormCheckBox id="intv-reassess-vitals-frequency-5-min" label="Every 5 minutes" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-reassess-vitals-frequency-15-min" label="Every 15 minutes" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-reassess-vitals-frequency-other" label="Other" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
            <li><i>Which vitals would you like to reassess?</i></li>
        </ul>
    )
}

export default ReassessVitalSigns
